<template>
  <v-container>
    <v-responsive max-width="100%">
      <v-container class="lighten-5">
        <v-row v-if="dataset.nombre">
          <v-col cols="12" sm="12">
            <label class="body-1">
              Buscando en <b>{{ dataset.nombre }}</b>
            </label>
            <label class="caption"> [{{ dataset.id }}] </label>
            <v-autocomplete
              v-model="valuesDSLearning"
              :items="learningDatasets"
              item-text="name"
              item-value="_id"
              dense
              filled
              rounded
              solo
              @change="selectLearningDataset()"
            >
            </v-autocomplete>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="10" sm="9">
            <v-text-field
              v-model="query"
              dense
              flat
              hide-details
              rounded
              solo-inverted
            ></v-text-field>
          </v-col>
          <v-col cols="1" sm="2">
            <v-btn class="mx-2" fab dark small color="primary" @click="search">
              <v-icon dark> fa-search </v-icon>
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-responsive>
    <v-divider class="mx-3 my-2"></v-divider>
    <v-list>
      <v-list-item v-for="(result, i) in searchResult" :key="i">
        <v-list-item-content>
          <v-list-item-title>
            <div class="title">{{ result.title }}</div>
            <div class="subheading text-no-wrap text-truncate">
              {{ result.text }}
            </div>
            <div class="caption link" text-truncate>
              <a :href="result.href">Ver documento</a>
            </div>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>

    <v-footer inset v-if="searchResult.length > 0">
      <div class="text-center">
        <v-pagination
          v-model="page"
          :length="15"
          :total-visible="5"
          prev-icon="fa-caret-left"
          next-icon="fa-caret-right"
        ></v-pagination>
      </div>
    </v-footer>
  </v-container>
</template>
<script>
const axios = require("axios");
import { mapGetters, mapMutations, mapState } from "vuex";
export default {
  name: "Search",
  data: () => ({
    page: 1,
    searchResult: [],
    learningDataset: "",
    learningItems: [],
    valuesDSLearning: "",
    query: "",
  }),
  methods: {
    search() {
      let query = this.query;
      this.searchResult = [];
      axios
        .post(`${this.urlServer}/prediction/${this.valuesDSLearning}`, {
          query,
        })
        .then((response) => {
          this.analisis = response.data.analisis;
          this.articulos = response.data.articulos;
          console.log(this.articulos);
          this.analisis.forEach((e) => {
            let artTemp = this.articulos.find((ar) => ar._id == e.label);
            this.searchResult.push({
              title: artTemp.articulo,
              text: artTemp.texto,
              href: `/reporte/${artTemp._id}`,
            });
          });
        });
    },
    selectLearningDataset() {
      //console.log(this.valuesDSLearning);
    },
  },
  updated: function () {
    this.$nextTick(function () {});
  },
  mounted: function () {
    this.$nextTick(function () {});
  },
  computed: {
    ...mapState(["urlServer"]),
    ...mapGetters("f", ["datasetActive", "dataset", "learningDatasets"]),
  },
};
</script>