<template>
  <v-container>
    <div class="document">
      <v-card>
        <v-card-title v-if="dataset.nombre" class="headline"
          >Dataset {{ dataset.nombre }}
        </v-card-title>
        <v-card-subtitle class="pb-0"> {{ dataset.id }} </v-card-subtitle>
        <v-card-actions>
          <template>
            <v-btn @click="onNewDataset">
              <v-icon color="green darken-2">fa-file-o</v-icon>
            </v-btn>
            <v-btn v-if="dataset.id" @click="onEditDataset">
              <v-icon color="green darken-2">fa-pencil-square-o</v-icon>
            </v-btn>
            <v-btn v-if="dataset.id" @click="isTrain = true">
              <v-icon color="green darken-2">fa-bolt</v-icon>
            </v-btn>
            <!--v-btn @click="isTest = true">
              <v-icon color="green darken-2">fa-commenting-o</v-icon>
            </v-btn-->
          </template>
        </v-card-actions>
        <v-spacer></v-spacer>
        <v-card-text class="text--primary">
          <div>Buscar resoluciones</div>
          <v-autocomplete
            v-model="nameResolutions"
            :items="resolutions"
            item-text="numero"
            item-value="_id"
            dense
            filled
            rounded
            solo
            @change="selectResolution($event)"
          >
          </v-autocomplete>
        </v-card-text>

        <v-card-text v-if="newDataset">
          <v-container class="lighten-5">
            <v-row>
              <v-col sm="12">
                <v-row no-gutters>
                  <v-col cols="6" sm="6">
                    <v-text-field
                      v-model="resolutionsAct.dependencia"
                      label="Dependencia"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="6" sm="6">
                    <v-text-field
                      v-model="resolutionsAct.numero"
                      label="Numero"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>

                <v-row no-gutters>
                  <v-col cols="6" sm="6">
                    <v-menu
                      ref="menu1"
                      v-model="menu1"
                      :close-on-content-click="false"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="auto"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="resolutionsAct.fecha"
                          label="Fecha"
                          hint="MM/DD/AAAA formato"
                          persistent-hint
                          prepend-icon="fa-calendar"
                          v-bind="attrs"
                          @blur="
                            resolutionsAct.fecha = parseDate(dateFormatted)
                          "
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="resolutionsAct.fecha"
                        no-title
                        @input="menu1 = false"
                      ></v-date-picker>
                    </v-menu>
                  </v-col>
                  <v-col cols="6" sm="6">
                    <v-text-field
                      v-model="resolutionsAct.autoridad"
                      label="Autoridad"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>

                <v-row no-gutters>
                  <v-col cols="6" sm="6">
                    <v-text-field
                      v-model="resolutionsAct.soporte"
                      label="Soporte"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="6" sm="6">
                    <v-select
                      v-model="resolutionsAct.tipo"
                      :items="itemsType"
                      label="Tipo"
                    ></v-select>
                  </v-col>
                </v-row>
                <v-row no-gutters>
                  <v-col cols="12" sm="12">
                    <v-text-field
                      v-model="resolutionsAct.descripcion"
                      label="Descripcion"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row no-gutters>
                  <v-col cols="12" sm="12">
                    <v-btn
                      color="success"
                      class="mr-4"
                      v-if="resolutionMode == 'save'"
                      @click="addResolutions"
                    >
                      Guardar
                    </v-btn>
                    <v-btn
                      color="warning"
                      class="mr-4"
                      v-if="resolutionMode == 'edit'"
                      @click="editResolutions"
                    >
                      Editar
                    </v-btn>
                    <v-btn color="error" class="mr-4"> Eliminar </v-btn>
                    <v-btn
                      color="grey lighten-2"
                      class="mr-4"
                      @click="newDataset = false"
                    >
                      Cancelar
                    </v-btn>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
            <v-row class="space-between"> </v-row>
          </v-container>
        </v-card-text>
        <!--Test :testing="isTest" @update_test="onUpdateTest" /-->
        <Articles />
      </v-card>
      <Training
        :training="isTrain"
        :dataset="dataset"
        @update_train="onUpdateTrain"
      />
    </div>
  </v-container>
</template>
<script>
const axios = require("axios");
import Articles from "../components/Articles";
import Training from "../components/Training";
//import Test from "../components/Test";
//import socket from "../socket";
//import store from "@/store";
import { mapGetters, mapMutations, mapState } from "vuex";
export default {
  name: "Document",
  components: {
    Articles,
    Training,
    //Test,
  },
  data: (vm) => ({
    document: {},
    isTrain: false,
    //isTest: false,
    currentComponent: "Articles",
    nameResolutions: "",
    resolutionMode: "edit",
    date: new Date().toISOString().substr(0, 10),
    dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
    menu1: false,
    itemsType: ["resolución", "acuerdo", "decreto"],
    idResolucionAct: "",
    resolutionsAct: {
      dependencia: "",
      numero: "",
      fecha: "",
      autoridad: "",
      soporte: "",
      descripcion: "",
      tipo: "",
    },
    numberResolutions: "",
    newDataset: false,
    editDataset: false,
    rules: [
      (value) => !!value || "Required.",
      (value) => (value && value.length >= 3) || "Min 3 characters",
    ],
  }),
  updated: function () {
    this.$nextTick(function () {});
  },
  mounted: function () {
    this.$nextTick(function () {});
  },
  methods: {
    getCorpusOfDocument() {
      this.$store.commit("f/filterCorpusById", this.datasetActive);
      this.$store.commit("f/setResolutions", this.datasetActive);
    },
    getResolutions() {
      this.$store.commit("f/setResolutions", this.datasetActive);
    },
    selectResolution(id) {
      this.idResolucionAct = id;
      this.resolutionsAct = this.resolutions.filter((r) => r._id == id)[0];
      this.$store.commit("f/setIdResolutions", id);
      this.$store.commit("f/setResolution", id);
      this.$store.commit("f/setArticulos", this.resolutionsAct.articulos);
      //this.newDataset = true;
      //this.resolutionMode = "edit";
    },
    addResolutions() {
      let self = this;
      let resolutionsAct = this.resolutionsAct;
      axios
        .post(`${this.urlServer}/addResolutions/${this.dataset.id}`, {
          resolutionsAct,
        })
        .then((response) => {
          if (response.data.status == 200) {
            //self.idResolutions = response.data.idResolucion;
            this.$store.commit(
              "f/setIdResolutions",
              response.data.idResolucion
            );
            self.newDataset = false;
            self.getResolutions();
          }
        });
    },
    editResolutions() {
      let self = this;
      axios
        .post(
          `${this.urlServer}/editResolutions/${this.idResolutions}`,
          this.resolutionsAct
        )
        .then((response) => {
          if (response.data.status == 200) {
            self.newDataset = false;
          }
        });
    },
    onNewDataset() {
      (this.resolutionMode = "save"), (this.newDataset = true);
      this.resolutionsAct = {
        dependencia: "",
        numero: "",
        fecha: "",
        autoridad: "",
        soporte: "",
        descripcion: "",
        tipo: "",
      };
    },
    onEditDataset() {
      if (this.idResolucionAct != "") {
        var id = this.idResolucionAct;
        this.resolutionsAct = this.resolutions.filter((r) => r._id == id)[0];
        this.$store.commit("f/setIdResolutions", id);
        this.$store.commit("f/setResolution", id);
        this.$store.commit("f/setArticulos", this.resolutionsAct.articulos);
        this.newDataset = true;
        this.resolutionMode = "edit";
      }
    },
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split("-");
      return `${month}/${day}/${year}`;
    },
    parseDate(date) {
      if (!date) return null;

      const [month, day, year] = date.split("/");
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
    onUpdateTrain(value) {
      this.isTrain = value;
    },
    onUpdateTest(value) {
      this.isTest = value;
    },
  },
  watch: {
    $route: "getCorpusOfDocument",
    date(val) {
      this.dateFormatted = this.formatDate(this.date);
    },
  },
  computed: {
    ...mapState(["urlServer"]),
    ...mapGetters("f", [
      "corpus",
      "datasetActive",
      "dataset",
      "resolutions",
      "idResolutions",
      "resolution",
    ]),
    computedDateFormatted() {
      return this.formatDate(this.date);
    },
  },
};
</script>