<template>
  <v-app id="inspire">
    <v-system-bar color="primary" app>
      <v-icon>fa-comment</v-icon>
      <span>10 datasets</span>
      <v-spacer></v-spacer>
      <v-icon :class="conected ? 'connected' : 'disconnected'">fa-plug</v-icon>
      <v-icon>fa-refresh</v-icon>
      <span>{{ server.name }} {{ server.version }}</span>
    </v-system-bar>
    <v-navigation-drawer v-model="drawer" app width="370">
      <v-sheet color="grey lighten-5" height="128" width="100%">
        <v-img
          :src="require('./assets/logo.png')"
          class="my-3"
          contain
          height="90%"
        />
      </v-sheet>

      <Folder />
    </v-navigation-drawer>

    <v-navigation-drawer app clipped width="380" right>
      <Search />
    </v-navigation-drawer>

    <v-main>
      <router-view></router-view>
    </v-main>

    <v-footer app color="transparent" height="72" inset> </v-footer>
  </v-app>
</template>


<script>
import Folder from "./components/Folder";
import Search from "./components/Search";
import Document from "./views/Document";
import socket from "./socket";

export default {
  name: "App",

  components: {
    Folder,
    Search,
    Document,
  },

  data: () => ({
    drawer: null,
    server: {
      name: "",
      version: "",
    },
    conected: false,
  }),
  created() {
    socket.connect();

    socket.on("connect", () => {
      this.conected = true;
    });

    // socket getters
    socket.emit("getVersion", {});

    // socket listeners
    socket.on("onVersion", (data) => {
      this.server.name = data.name;
      this.server.version = data.version;
      if (data) this.conected = true;
    });

    socket.on("disconnect", (data) => {
      this.conected = false;
    });
  },
  mounted() {},
};
</script>
<style scoped>
.connected {
  color: #76ff03 !important;
}

.disconnected {
  color: #616161 !important;
}
</style>
