<template>
  <v-row justify="center">
    <v-dialog v-model="isTrain" persistent max-width="600px">
      <!--template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark v-bind="attrs" v-on="on">
          Open Dialog
        </v-btn>
      </template-->
      <v-card>
        <v-card-text>
          <span class="headline">Dataset {{ dataset.nombre }}</span>
