<script>
const axios = require("axios");
import { mapGetters, mapMutations, mapState } from "vuex";
import socket from "../socket";

export default {
  name: "Training",
  props: {
    dataset: Object,
    training: Boolean,
  },
  data: () => ({
    idEvent: 0,
    events: [],
    nonce: 0,
    nameTrain: "",
    events: [],
    testTrain: 30,
  }),
  created() {
    socket.on("addItem", (data) => {
      data.id = this.idEvent++;
      this.events.push(data);
    });
  },
  methods: {
    setTrain() {
      this.events = [];
      axios.post(`${this.urlServer}/setDataset/${this.dataset.id}`, {
        testrain: this.testTrain,
        nameTrain: this.nameTrain,
      });
    },
    setTraining(val) {
      this.$emit("update_train", val);
    },
    /*getDataset() {
      axios
        .get(`${this.urlServer}/getDataset/${this.dataset.id}`)
        .then((response) => {
          console.log(response);
        });
    },*/
  },
  updated: function () {
    this.$nextTick(function () {
      //this.training ? this.getDataset() : false;
    });
  },
  computed: {
    ...mapState(["urlServer"]),
    isTrain: function () {
      return this.training;
    },
    timeline() {
      return this.events.slice().reverse();
    },
    /*getDatasetsOfDocument() {
      this.getDataset();
    }*/
  },
};
</script>