<template>
  <v-container>
    <v-data-table
      :headers="headers"
      :items="articulos"
      sort-by="calories"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Artículos</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                Nuevo Artículo
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.numeracion"
                        label="Numeracion"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.etiqueta"
                        label="Etiqueta"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.articulo"
                        label="Artículo"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.texto"
                        label="Texto"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="headline"
                >Are you sure you want to delete this item?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> fa-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> fa-trash </v-icon>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize"> Reset </v-btn>
      </template>
    </v-data-table>
  </v-container>
</template>
<script>
import { mapGetters, mapMutations, mapState } from "vuex";
const axios = require("axios");

export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    idResolution: "",
    headers: [
      {
        text: "Id",
        value: "_id",
      },
      {
        text: "Numeración",
        value: "numeracion",
        align: "start",
        sortable: false,
      },
      { text: "Articulo", value: "articulo" },
      { text: "Etiqueta", value: "etiqueta" },
      { text: "Texto", value: "texto" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    editedIndex: -1,
    editedItem: {
      numeracion: "",
      articulo: "",
      etiqueta: "",
      texto: "",
    },
    defaultItem: {
      numeracion: "",
      articulo: "",
      etiqueta: "",
      texto: "",
    },
  }),

  updated: function () {},
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  "$store.state": "refreshResolution",
  created() {
    this.initialize();
  },

  methods: {
    refreshResolution() {
      console.log("resolu");
      this.articulos = this.resolution.articulos;
    },

    initialize() {
      //this.articulos = [];
    },

    editItem(item) {
      this.editedIndex = this.articulos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.articulos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.editedItem._id = item._id;
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      axios.post(
        `${this.urlServer}/deleteArticles/${this.idResolutions}/${this.editedItem._id}`,
        (err, data) => {
          console.log(data);
          if (err) throw err;
          this.articulos.splice(this.editedIndex, 1);
          this.closeDelete();
          this.dialogDelete = false;
        }
      );
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = true;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      let editedItem = this.editedItem;
      if (this.editedIndex > -1) {
        Object.assign(this.articulos[this.editedIndex], this.editedItem);
        axios.post(`${this.urlServer}/updateArticles/${this.idResolutions}`, {
          editedItem,
        });
      } else {
        this.articulos.push(this.editedItem);
        axios.post(`${this.urlServer}/addArticles/${this.idResolutions}`, {
          editedItem,
        });
      }
      this.close();
    },
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },

    ...mapGetters("f", ["idResolutions", "resolution", "articulos"]),
    ...mapState(["urlServer"]),
  },
};
</script>      