<template>
  <v-container>
    <v-layout wrap>
      <v-flex xs12>
        <div class="headline">Datasets</div>
        <v-autocomplete
          v-model="corpusSelected"
          :items="corpus"
          item-text="nombre"
          item-value="id"
          dense
          filled
          rounded
          solo
          @change="selectDocument($event)"
        ></v-autocomplete>
        <v-card>
          <v-card-title>New dataset</v-card-title>
          <v-card-text class="secondary">
            <form>
              <v-text-field
                v-model="nameDataset"
                :counter="100"
                label="Training dataset name"
                required
              ></v-text-field>
              <v-btn class="mr-4" @click="onNewCorpus"> Guardar </v-btn>
              <v-btn @click="clear"> Cancelar </v-btn>
            </form>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex mb-12> </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
const axios = require("axios");
import { mapGetters, mapMutations, mapState } from "vuex";
import socket from "../socket";

export default {
  name: "Folder",
  data: () => ({
    idClient: "60659f6995c523d8451e09cf",
    nameDataset: "",
    corpusSelected: "",
  }),
  created() {
    socket.on("onNewCorpus", (data) => {});
  },
  updated: function () {
    this.$nextTick(function () {});
  },
  mounted: function () {
    this.$nextTick(function () {
      this.refresCorpus();
    });
  },
  methods: {
    refresCorpus() {
      this.$store.dispatch("f/setCorpus");
    },
    onNewCorpus() {
      let self = this;
      let idClient = this.idClient;
      let urlServer = this.$store.state.urlServer;
      axios
        .post(`${urlServer}/onNewCorpus/${idClient}`, {
          name: self.nameDataset,
        })
        .then((response) => {
          let id = response.data.id;
          if (response.data.status == 200) {
            // actualizar dataset activo
            this.$store.commit("f/setDatasetActive", id);
            // actualizar listado de corpus
            this.$store.dispatch("f/setCorpus");
            // actualizar vista de documento
            this.$router.push({ path: `/document/${id}` }).catch(() => {});
          }
        });
    },
    clear() {
      this.nameDataset = "";
    },
    selectDocument(id) {
      this.$store.commit("f/setDatasetActive", id);
      this.$store.commit("f/filterCorpusById", id);
      this.$store.commit("f/setResolutions", this.datasetActive);
      this.$router.push({ path: `/document/${id}` }).catch(() => {});
    },
  },
  computed: {
    ...mapState(["urlServer"]),
    ...mapGetters("f", ["corpus", "datasetActive"]),
    ...mapMutations("f", ["setCorpus"]),
  },
};
</script>