const axios = require("axios");

export default {
  namespaced: true,
  state: {
    urlServer: "https://colegalnode.herokuapp.com",
    corpus: [],
    datasetActive: "#",
    dataset: {},
    idResolutions: null,
    resolutions: [],
    resolution: {},
    articulos: [],
    learningDatasets: [],
    itemsLearning: [],
    valuesLearning: [],
  },
  getters: {
    corpus(state) {
      return state.corpus;
    },
    datasetActive(state) {
      return state.datasetActive;
    },
    dataset(state) {
      return state.dataset;
    },
    resolutions(state) {
      return state.resolutions;
    },
    idResolutions(state) {
      return state.idResolutions;
    },
    resolution(state) {
      return state.resolution;
    },
    articulos(state) {
      return state.articulos;
    },
    learningDatasets(state) {
      return state.learningDatasets;
    },
    itemsLearning(state) {
      return state.itemsLearning;
    },
    valuesLearning(state) {
      return state.valuesLearning;
    },
  },
  mutations: {
    setCorpus(state) {
      axios.get(`${state.urlServer}/onGetAllCorpus/`).then((response) => {
        state.corpus = response.data.status == 200 ? response.data.corpus : [];
      });
    },
    setDatasetActive(state, id) {
      state.datasetActive = id;
    },
    setResolutions(state, id) {
      axios.get(`${state.urlServer}/getResolutions/${id}`).then((response) => {
        state.resolutions =
          response.data.status == 200 ? response.data.resoluciones : [];
        state.learningDatasets =
          response.data.status == 200 ? response.data.learningDatasets : [];
      });
    },
    filterCorpusById(state, id) {
      state.dataset = state.corpus.filter((c) => c.id == id)[0];
    },
    setIdResolutions(state, id) {
      state.idResolutions = id;
    },
    setResolution(state, id) {
      state.resolution = state.resolutions.filter((r) => r._id == id)[0];
    },
    setArticulos(state, articulos) {
      state.articulos = articulos;
    },
    setLearningDatasets(state, learningDatasets) {
      state.learningDatasets = learningDatasets;
    },
  },
  actions: {
    setCorpus(context) {
      context.commit("setCorpus");
    },
    setDatasetActive(context) {
      context.commit("setDatasetActive");
    },
    filterCorpusById(context) {
      context.commit("filterCorpusById");
    },
    setResolutions(context) {
      context.commit("setResolutions");
    },
    setIdResolutions(context) {
      context.commit("setIdResolutions");
    },
    setResolution(context) {
      context.commit("setResolution");
    },
    setArticulos(context) {
      context.commit("setArticulos");
    },
    setLearningDatasets(context) {
      context.commit("setLearningDatasets");
    },
  },
  modules: {},
};
